.navbar {
  /* background-color: #8739f9; */
  background-color: #030708;
  height: 85px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  position: sticky;
  top: 0;
  z-index: 9;
  /* border-bottom: 5px solid #fff; */
}

.logo {
  height: 70px;
  width: 70px;
}
.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.nav-logo {
  color: #fff;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.5rem;
  flex-grow: 1;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #8739f9;
}

.nav-item .active {
  color: #8739f9;
  border: 2px solid#8739f9;
  border-radius: 5px;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    z-index: 9999;
    display: flex;
    flex-direction: column;
    width: 99%;
    height: 100vh;
    /* border-top: 1pxsolid #fff; */
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    /* transform: translate(-100%); */
    transition: all 0.45s ease;
  }

  .nav-menu.active {
    background: #030708;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-item .active {
    color: #8739f9;
    border: none;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.5rem;
    cursor: pointer;
    color: #8739f9;
  }
}

.footer {
  margin-top: 25px;
  margin-bottom: 10px;
}

.name-link {
  color: #8739f9;
  text-decoration: none;
}
