.loader-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
}

.loader-container > h1 {
  padding: 5px;
  position: relative;
  font-size: 8rem;
  color: #222;
  text-transform: uppercase;
  /* border: 3px solid #222; */
  /* letter-spacing: 0.05em; */
  /* line-height: 1.2em; */
  font-family: "Poppins", sans-serif;
}
.loader-container > h1:before {
  padding: 5px;

  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: #8739f9;
  overflow: hidden;
  border: 0px solid #8739f9;
  -webkit-animation: animate 2.5s linear infinite;
          animation: animate 2.5s linear infinite;
}
@-webkit-keyframes animate {
  0% {
    width: 0;
    border: 2px solid #8739f9;
  }
  100% {
    width: 100%;
    border: 2px solid #8739f9;
  }
}
@keyframes animate {
  0% {
    width: 0;
    border: 2px solid #8739f9;
  }
  100% {
    width: 100%;
    border: 2px solid #8739f9;
  }
}

